@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Plus+Jakarta+Sans:wght@400;500;600&display=swap");
.contener {
  max-width: 528px;
  margin: 0 auto;
}

.wrapper60px {
  padding: 60px 0px 60px 0px;
}

.header__info {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-bottom: 48px;
}
.header__info p {
  font-size: 1.125rem;
}

.projects__wrapper {
  display: flex;
  flex-direction: column;
  gap: 64px;
}
.projects__resent {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.projects__shots {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.projects__shots .projects__description {
  max-width: 382px;
}
.projects__item {
  background-color: #272727;
  padding: 25px 30px 0px 30px;
  max-height: 347px;
  overflow: hidden;
  border-radius: 4px;
}
.projects__item:hover {
  cursor: pointer;
}
.projects__item:hover .projects__info-button {
  background: rgba(255, 255, 255, 0.16);
}
@media (max-width: 500px) {
  .projects .projects__item {
    max-height: 320px;
    padding: 20px 20px 0px 20px;
  }
}
.projects__info {
  display: flex;
  justify-content: space-between;
}
.projects .projects__info-button {
  display: flex;
  width: 50px;
  height: 50px;
  padding: 19.38px;
  justify-content: center;
  align-items: center;
  gap: 13.843px;
  flex-shrink: 0;
  border-radius: 138.427px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: background-color 150ms ease-in;
}
.projects__info-text {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-right: 20px;
}
.projects__caption {
  display: flex;
  align-items: flex-end;
  gap: 12px;
}
.projects__name {
  color: #fff;
  font-family: Plus Jakarta Sans;
  font-weight: 600;
  line-height: 100%;
}
.projects__date {
  color: #FFF;
  font-family: Plus Jakarta Sans;
  font-size: 0.75rem;
  font-weight: 600;
  line-height: 100%;
}
.projects__description {
  color: #FFF;
  opacity: 0.8;
  font-family: Plus Jakarta Sans;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 140%;
}
.projects__img {
  padding-top: 26px;
}
.projects__img img {
  width: 100%;
  max-width: 469px;
}